<template>
    <nacl-page-container no-header>
        <!-- <nacl-header>中奖记录</nacl-header> -->
        <nacl-page-content
            no-footer
            class="content-wrap"
        >
            <ul v-if="listData.length">
                <li v-for="(item, index) in listData" :key="index" class="item">
                    <div class="img"><img :src="item.zjimg" alt=""></div>
                    <p class="titel">{{ item.zjname }}</p>
                </li>
            </ul>
            <div v-else>
                <img
                    class="cart-no-data"
                    src="../assets/cart-no-data.png"
                    width="100%"
                >
            </div>
        </nacl-page-content>
    </nacl-page-container>
</template>

<script>
import { findzj } from '../services'

export default {
    name: '',
    components: {},
    data () {
        return {
            listData: []
        }
    },
    computed: {},
    async created () {
        this.listData = await findzj()
        console.log(this.listData)
    },
    methods: {
        goRouting(param){
            this.toGetUser();
            window.webkit.messageHandlers.goRouting.postMessage(param);
        },
        toGetUser(){
            const cutCount = Math.ceil(JSON.stringify(localStorage).length / 1000);
            for(let i = 0 ; i<cutCount;i++){
                if(i!=cutCount-1){
                    if(i==0){
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }else{
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }
                    
                }else{
                    window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,JSON.stringify(localStorage).length)));
                }
            }
        },
    },
    mounted(){
        window.goRouting = this.goRouting;
    }
}
</script>

